import React from "react";
let Settings = (props) => {
  let delet = document.querySelector("body");

  return (
    <div>
      <div>вот это</div>
      <button
        onClick={() => {
          delet.remove();
        }}
      >
        delete
      </button>
      <div>или вот это</div>
    </div>
  );
};
export default Settings;
